import React, { Component } from 'react';
import sounds from '../audio/bard';
import * as items from '../data/items.json';
import * as weapons from '../data/weapons.json';

class Item extends Component {
    state = {
        gold: 70,
        equipment:[
            items.QuestItems.IsmenaBook,
            items.QuestItems.RoomKey,
            items.Items.HealthPotions,
            items.Items.Bag,
            items.Items.FoodRations,
            weapons.OneHanded.Dagger
        ]
    }

    handlePlaySound = id => {
        const { item, potion, book, gold, dagger } = sounds;
        switch(id) {
            case 'item':
                item.play();
                break;
            case 'gold':
                gold.play();
                break;
            case 'potion':
                potion.play();
                break;
            case 'book':
                book.play();
                break;
            case 'dagger':
                dagger.play();
                break;
            default:
                item.play();
        }
    }

    render() { 
        return (
            <React.Fragment>
                <div className="content-equipment_item">
                <ul>
                    <li class="item" onClick={() => this.handlePlaySound('gold')}>Dostępne złoto: {this.state.gold} sz</li>
                    {this.state.equipment.map(m => <li key={Math.random()} class="item" onClick={() => this.handlePlaySound(m[4])}>Nazwa: {m[0]}, Waga: {m[1]}, Cena: {m[2]}, Opis: {m[3]}</li>)}
                </ul>
                </div>
            </React.Fragment>
        );
    }
}
 
export default Item;